<template>
  <div class="chess">
    <div class="box">
      <h2>五子棋 轮到 {{isFirst?'黑棋':'白棋'}}</h2>
      <h3>当前 {{!isComputer?'人人':'人机'}}对战</h3>
      <canvas @click.stop="chessClick" ref="canvasDom"></canvas>
      <div class="btn">
        <fx-button type="default" @clickEvt="computeClick">{{isComputer?'人人':'人机'}}对战</fx-button>
        <fx-button type="default" @clickEvt="initData">重置</fx-button>
      </div>
    </div>
    <div class="winner" v-if="modal" @click.stop="initData">
      <p>{{winner==='black'?'黑棋获胜':'白棋获胜'}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'fiveChess',
  data() {
    return {
      // 棋盘 大小
      broad: {
        row: 21,
        col: 21
      },
      // 初始化数据
      initInfo: {
        width: 0,
        height: 0,
        row: 0,
        col: 0
      },
      // 画布
      context: null,
      // 坐标位置
      axis: [],
      // 是否是先手
      isFirst: true,
      // 胜利弹窗
      modal: false,
      // 胜利者
      winner: '',
      // 是否电脑
      isComputer: true
    }
  },
  methods: {
    // 初始化数据
    initData() {
      this.clear()
      this.aiData()
      let canvas = this.$refs.canvasDom
      this.context = canvas.getContext('2d')
      this.initInfo.width = canvas.clientWidth
      this.initInfo.height = canvas.clientHeight
      this.initInfo.row = canvas.clientWidth / this.broad.row
      this.initInfo.col = canvas.clientHeight / this.broad.col
      this.init(canvas)
      this.modal = false
    },
    // 初始化画布
    init(canvas) {
      canvas.width = canvas.clientWidth
      canvas.height = canvas.clientHeight
      this.paintBroad()
    },
    // 画棋盘
    paintBroad(context = this.context) {
      let canvas = this.$refs.canvasDom
      // 画row
      for (let i = 1; i < this.broad.row; i++) {
        context.strokeStyle = '#000'
        context.lineWidth = 1
        context.beginPath()
        context.moveTo(this.initInfo.col, this.initInfo.row * i)
        context.lineTo(this.initInfo.width - this.initInfo.col, this.initInfo.row * i)
        context.stroke()
        context.closePath()
      }
      // 画col
      for (let i = 1; i < this.broad.col; i++) {
        context.strokeStyle = '#000'
        context.lineWidth = 1
        context.beginPath()
        context.moveTo(this.initInfo.col * i, this.initInfo.row)
        context.lineTo(this.initInfo.col * i, this.initInfo.height - this.initInfo.row)
        context.stroke()
        context.closePath()
      }
      // 设定点数据
      const row = this.broad.row - 1
      const col = this.broad.col - 1
      const len = row * col
      for (let i = 0; i < col; i++) {
        this.axis.push([])
        for (let j = 0; j < row; j++) {
          this.axis[i].push({
            x: ((j % row) + 1) * this.initInfo.row,
            y: (i + 1) * this.initInfo.col,
            isBeing: false,
            chessType: 0
          })
        }
      }
    },
    /* 画棋子
     * x:画圆x轴
     * y:画圆y轴
     * isOpponent: 是否是对手棋子
     */
    paintChess(x, y, isOpponent = this.isFirst, context = this.context) {
      let r = Math.min(this.initInfo.row, this.initInfo.col) / 2
      context.beginPath()
      let grd = context.createRadialGradient(x, y, 1, x, y, r)
      if (!isOpponent) {
        grd.addColorStop(0, '#fff')
        grd.addColorStop(1, '#ccc')
      } else {
        grd.addColorStop(0, '#ccc')
        grd.addColorStop(1, '#000')
      }
      context.fillStyle = grd
      context.arc(x, y, r, 0, 2 * Math.PI)
      context.fill()
      context.closePath()
      this.isFirst = !this.isFirst
    },
    // 下棋
    chessClick(ev) {
      let axisData = this.computeAxis(ev.offsetX, ev.offsetY)
      if (axisData) this.paintChess(axisData.x, axisData.y)
      let isWin = this.isWin()
      if (isWin) {
        this.winner = isWin
        this.modal = true
      }
      if (this.isComputer) this.computer(axisData)
    },
    // 判断点击位置
    computeAxis(x, y) {
      let obj = null
      this.axis.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          let disX = Math.abs(child.x - x)
          let disY = Math.abs(child.y - y)
          let disR = Math.sqrt(disX * disX + disY * disY)
          const r = Math.min(this.initInfo.row, this.initInfo.col) / 2
          if (disR <= r && !child.isBeing) {
            obj = Object.assign(child, { row: itemIndex, col: childIndex })
            child.isBeing = true
            child.chessType = this.isFirst ? 1 : 2
          }
        })
      })
      return obj
    },
    // 查看
    findArr() {
      let arr = []
      this.axis.forEach((item, itenIndex) => {
        arr.push([])
        item.forEach(child => {
          arr[itenIndex].push(child.chessType)
        })
      })
      return arr
    },
    // 是否获胜
    isWin() {
      let arr = this.findArr()
      let win = null
      arr.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          if (child === 1 && !win) win = this.winGame(arr, item, itemIndex, childIndex, 1)
          if (child === 2 && !win) win = this.winGame(arr, item, itemIndex, childIndex, 2)
        })
      })
      return win
    },
    winGame(arr, item, itemIndex, childIndex, type) {
      let text = null
      if (this.winGameTypeRow(item, childIndex, type) && !text)
        text = type === 1 ? 'black' : 'white'
      else if (this.winGameTypeCol(arr, itemIndex, childIndex, type) && !text)
        text = type === 1 ? 'black' : 'white'
      else if (this.winGameTypeRight(arr, itemIndex, childIndex, type) && !text)
        text = type === 1 ? 'black' : 'white'
      else if (this.winGameTypeLeft(arr, itemIndex, childIndex, type) && !text)
        text = type === 1 ? 'black' : 'white'
      return text
    },
    // 横向胜利方法
    winGameTypeRow(arr, index, player) {
      let winArr = []
      for (let i = 1; i < 5; i++) {
        if (arr[index + i]) winArr.push(arr[index + i])
      }
      if (winArr.length < 4) return false
      return winArr.every(item => {
        return item === Number(player)
      })
    },
    // 纵向胜利方法
    winGameTypeCol(arr, ind, index, player) {
      let winArr = []
      for (let i = 1; i < 5; i++) {
        if (arr[ind + i] && arr[ind + i][index]) winArr.push(arr[ind + i][index])
      }
      if (winArr.length < 4) return false
      return winArr.every(item => {
        return item === Number(player)
      })
    },
    // 右斜下向胜利方法
    winGameTypeRight(arr, ind, index, player) {
      let winArr = []
      for (let i = 1; i < 5; i++) {
        if (arr[ind + i] && arr[ind + i][index + i]) winArr.push(arr[ind + i][index + i])
      }
      if (winArr.length < 4) return false
      return winArr.every(item => {
        return item === Number(player)
      })
    },
    // 左斜下向胜利方法
    winGameTypeLeft(arr, ind, index, player) {
      let winArr = []
      for (let i = 1; i < 5; i++) {
        if (arr[ind + i] && arr[ind + i][index - i]) winArr.push(arr[ind + i][index - i])
      }
      if (winArr.length < 4) return false
      return winArr.every(item => {
        return item === Number(player)
      })
    },
    // 电脑
    aiData() {
      let row = this.broad.row - 1
      let col = this.broad.col - 1
      this.count = 0
      this.allWin = []
      for (let i = 0; i < row; i++) {
        this.allWin[i] = []
        for (let j = 0; j < col; j++) {
          this.allWin[i][j] = []
        }
      }
      for (let i = 0; i < row; i++) {
        for (let j = 0; j < col - 4; j++) {
          for (let k = 0; k < 5; k++) {
            this.allWin[j + k][i][this.count] = true
          }
          this.count++
        }
      }
      for (let i = 0; i < row; i++) {
        for (let j = 0; j < col - 4; j++) {
          for (let k = 0; k < 5; k++) {
            this.allWin[i][j + k][this.count] = true
          }
          this.count++
        }
      }
      for (let i = 0; i < row - 4; i++) {
        for (let j = 0; j < col - 4; j++) {
          for (let k = 0; k < 5; k++) {
            this.allWin[i + k][j + k][this.count] = true
          }
          this.count++
        }
      }
      for (let i = row - 1; i > 3; i--) {
        for (let j = 0; j < row - 4; j++) {
          for (let k = 0; k < 5; k++) {
            this.allWin[i - k][j + k][this.count] = true
          }
          this.count++
        }
      }
      this.myWin = []
      this.compWin = []
      for (let i = 0; i < this.count; i++) {
        this.myWin[i] = 0
        this.compWin[i] = 0
      }
    },
    // 电脑下棋
    computerChess() {
      let myScore = []
      let compScore = []
      for (let i = 0; i < this.broad.row - 1; i++) {
        myScore[i] = []
        compScore[i] = []
        for (let j = 0; j < this.broad.col - 1; j++) {
          myScore[i][j] = 0
          compScore[i][j] = 0
        }
      }
      let max = 0
      let maxX = 0
      let maxY = 0
      for (let i = 0; i < this.broad.row - 1; i++) {
        for (let j = 0; j < this.broad.col - 1; j++) {
          if (!this.axis[i][j].isBeing) {
            for (let k = 0; k < this.count; k++) {
              if (this.allWin[i][j][k]) {
                if (this.myWin[k] === 1) {
                  myScore[i][j] += 200
                } else if (this.myWin[k] === 2) {
                  myScore[i][j] += 500
                } else if (this.myWin[k] === 3) {
                  myScore[i][j] += 2000
                } else if (this.myWin[k] === 4) {
                  myScore[i][j] += 50000
                }
                if (this.compWin[k] === 1) {
                  compScore[i][j] += 300
                } else if (this.compWin[k] === 2) {
                  compScore[i][j] += 800
                } else if (this.compWin[k] === 3) {
                  compScore[i][j] += 20000
                } else if (this.compWin[k] === 4) {
                  compScore[i][j] += 500000
                }
              }
            }

            if (myScore[i][j] > max) {
              max = myScore[i][j]
              maxX = i
              maxY = j
            }
            if (compScore[i][j] > max) {
              max = compScore[i][j]
              maxX = i
              maxY = j
            }
          }
        }
      }
      let x = this.axis[maxX][maxY].x
      let y = this.axis[maxX][maxY].y
      let axisData = this.computeAxis(x, y)
      if (axisData) this.paintChess(axisData.x, axisData.y)
      let isWin = this.isWin()
      if (isWin) {
        this.winner = isWin
        this.modal = true
      }
      for (var k = 0; k < this.count; k++) {
        if (this.allWin[maxX][maxY][k]) {
          this.compWin[k]++
          this.myWin[k] = 6
        }
      }
    },
    // 清空数据
    clear() {
      this.context = null
      this.axis = []
      this.isFirst = true
      this.modal = false
      this.winner = ''
    },
    // 是否开始下棋
    isBegin() {
      let arr = this.findArr()
      let obj = { n: 0 }
      arr.forEach((item, itemIndex) => {
        return item.forEach((child, childIndex) => {
          if (child === 1) {
            obj.n++
            obj.row = itemIndex
            obj.col = childIndex
          }
        })
      })
      return obj
    },
    // 电脑下棋
    computer(axisData) {
      for (var k = 0; k < this.count; k++) {
        if (this.allWin[axisData.row][axisData.col][k]) {
          this.myWin[k]++
          this.compWin[k] = 6
        }
      }
      let obj = this.isBegin()
      if (obj.n === 1) {
        let x =
          Math.random() > 0.5
            ? this.axis[obj.row - 1][obj.col].x
            : this.axis[obj.row + 1][obj.col].x
        let y =
          Math.random() > 0.5
            ? this.axis[obj.row - 1][obj.col].y
            : this.axis[obj.row + 1][obj.col].y
        let axisData = this.computeAxis(x, y)
        if (axisData) this.paintChess(axisData.x, axisData.y)
      } else {
        this.computerChess()
      }
    },
    // 切换人机 人人
    computeClick() {
      if (this.isBegin().n) return
      this.isComputer = !this.isComputer
    }
  },
  mounted() {
    this.initData()
  }
}
</script>

<style lang="less" scoped>
.chess {
  width: 100%;
  height: 100%;
  position: relative;
  > .box {
    width: fit-content;
    height: fit-content;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    text-align: center;
    > h2 {
      font-size: 20px;
    }
    > h3 {
      padding: 10px 0;
      font-size: 16px;
    }
    > canvas {
      display: block;
      width: 600px;
      height: 600px;
      border: 1px solid #ccc;
      background-color: #edb666;
      box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.5);
    }
    > .btn {
      padding-top: 20px;
    }
  }
  > .winner {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    > p {
      width: fit-content;
      height: fit-content;
      position: absolute;
      bottom: 0;
      top: 0;
      right: 0;
      margin: auto;
      font-size: 48px;
      background-color: #fff;
    }
  }
}
</style>