<template>
  <div class="home">
    <div class="box">
      <div class="wrapper">
        <ul>
          <li v-for="(item,itemIndex) in gameList" :key="itemIndex">
            <span v-for="(child,childIndex) in item" :key="childIndex"></span>
          </li>
        </ul>
        <ol>
          <li v-for="(item,itemIndex) in numArr" :key="itemIndex">
            <em
              v-for="(child,childIndex) in item"
              :key="childIndex"
              :style="{left:`${child.left}px`,top:`${child.top}px`}"
              :class="`_${child.num}`"
            >{{child.num}}</em>
          </li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
import { log } from 'util'
export default {
  name: 'home',
  data() {
    return {
      gameList: [[], [], [], []],
      numArr: [[], [], [], []]
    }
  },
  methods: {
    initBox() {
      this.setPosition()
    },
    randomPiece() {
      const x = Math.floor(Math.random() * 4)
      const y = Math.floor(Math.random() * 4)
      !this.numArr[x][y] || this.numArr[x][y].num === 0
        ? (this.numArr[x][y].num = 2)
        : this.randomPiece()
    },
    setPosition() {
      let arr = Array.from(new Array(16))
      this.gameList = [[], [], [], []]
      arr.forEach((item, index) => {
        let ind = parseInt(Math.floor(index / 4))
        this.gameList[ind].push({
          left: (index % 4) * 110,
          top: Math.floor(index / 4) * 110
        })
      })
      this.randomPiece()
      this.randomPiece()
      console.log(this.numArr)
    },
    keydown(ev) {
      this.run()
      // this.randomPiece()
    },
    right(arr) {
      let anyChange = false
      for (let i = 0; i < arr.length; i++) {
        var firstIndex = 0
        var secondIndex = 1
        while (secondIndex < 4) {
          if (arr[i][firstIndex].num == 0) {
            firstIndex++
            secondIndex++
            continue
          }
          if (arr[i][secondIndex].num == 0) {
            secondIndex++
            continue
          } else if (arr[i][firstIndex].num == arr[i][secondIndex].num) {
            arr[i][firstIndex].num = arr[i][firstIndex].num * 2
            arr[i][secondIndex].left = arr[i][firstIndex].left
            arr[i][secondIndex].num = 0
            anyChange = true
            if (arr[i][firstIndex].num >= 2048) {
              gameSuccess = true
            }
            break
          } else {
            firstIndex = secondIndex
            secondIndex += 1
            continue
          }
        }
      }
    },
    isZero(arr) {},
    run() {
      let arr = this.gameList
      this.right(arr)
      // this.gameList = JSON.parse(JSON.stringify(arr))
    }
  },
  mounted() {
    this.initBox()
    window.addEventListener('keydown', this.keydown, true)
  },
  destroyed() {
    window.removeEventListener('keydown', this.keydown, true)
  }
}
</script>

